<template>
  <div class="app">
    <header>
      <div class="title">
        <img src="./assets/Screenshot.png" alt="site logo">
        <h1>Demo Project</h1>
      </div>
      <div class="order">
        <button @click="handleClick('title')">Order by title</button>
        <button @click="handleClick('salary')">Order by salary</button>
        <button @click="handleClick('location')">Order by location</button>
      </div>
    </header>

    <JobList :jobs="jobs" :order="order" />
  </div>
</template>

<script lang="ts">
import { defineComponent, ref } from 'vue'
import Job from '@/types/Job'
import OrderTerm from '@/types/OrderTerm'
import JobList from './components/jobsList.vue'

export default defineComponent({
  name: 'App',
  components: { JobList },
  setup() {
    const jobs = ref<Job[]>([
      { title: 'A Lorem Ipsum is simply dummy', location: 'ALorem Lipsum Dummy', salary: 2000, id: '1' },
      { title: 'B Industry. Lorem Ipsum has', location: 'BLorem Lipsum Dummy', salary: 5000, id: '2' },
      { title: 'C text of the printing and typesetting', location: 'CLorem Lipsum Dummy', salary: 4000, id: '3' },
      { title: 'D been the industrys standard dummy', location: 'DLorem Lipsum Dummy', salary: 1000, id: '4' },
      { title: 'E Lorem Ipsum is simply dummy', location: 'ELorem Lipsum Dummy', salary: 3000, id: '5' }
    ])
    const order = ref<OrderTerm>('title')

    const handleClick = (term: OrderTerm) => {
      order.value = term
    }

    return { jobs, order, handleClick }
  }
});
</script>

